/**
 * System imports
 ============================================================================ */
import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Image from "react-bootstrap/Image";

/**
  * User imports
  ============================================================================ */
import Summary from "../summary";
import Alert from "../alert";
import Login from "../login";
import Logout from "../logout";
import SignUp from "../signup";
import PasswordReset from "../passwordReset";
import ValidateEmail from "../validateEmail";
import ChangePassword from "../changePassword";
import Auth from "../auth";
import Footer from "../footer";
import MainMenu from "../mainMenu";
import Help from "../help";
import Article from "../article";
import Jobs from "../jobs";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { signedIn } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getRedirect, getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module constats
   ============================================================================ */
  const { wrapperClass, innerClass } = getContainerClass(signedIn);
  const appClass = signedIn ? "App" : "App bg";

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <>
        <Router>
          {getRedirect()}

          <div className={appClass} style={{}}>
            {/**
             * The alert
             */}
            <Alert data={args} />

            {/**
             * Wrapper for the main window
             */}
            <div className={wrapperClass} style={{}}>
              {/**
               * The main window
               */}
              <div
                className={innerClass}
                style={{
                  backgroundColor: "Transparent",
                }}
              >
                <MainMenu {...args} />

                {(window.location.pathname === "/sign-in" ||
                  window.location.pathname === "/sign-out" ||
                  (window.location.pathname === "/" && !signedIn)) && (
                  <Image
                    className="d-none d-md-block"
                    src="gfx/raised_hands.png"
                    fluid
                  />
                )}

                {/**
                 * Routes
                 */}
                <Switch>
                  {/**
                   * Route: Sign up
                   */}
                  <Route
                    exact
                    path="/sign-up"
                    component={() => {
                      return SignUp(args);
                    }}
                  />

                  {/**
                   * Route: Sign up
                   */}
                  <Route
                    exact
                    path="/auth"
                    component={() => (signedIn ? Auth(args) : Login(args))}
                  />

                  {/**
                   * Route: Validate mail
                   */}
                  <Route
                    exact
                    path="/validate-email"
                    component={() => {
                      return ValidateEmail(args);
                    }}
                  />

                  {/**
                   * Route: Change password
                   */}
                  <Route
                    exact
                    path="/password"
                    component={() => {
                      return ChangePassword(args);
                    }}
                  />

                  {/**
                   * Route: Sign out
                   */}
                  <Route
                    exact
                    path="/sign-out"
                    component={() => {
                      return Logout(args);
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    exact
                    path="/jobs"
                    component={() => {
                      return Jobs(args);
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    exact
                    path="/help"
                    component={() => {
                      return Help(args);
                    }}
                  />

                  {/**
                   * Route: Help Topic
                   */}
                  <Route
                    path="/help/topic"
                    component={() => {
                      return Help(args);
                    }}
                  />

                  {/**
                   * Route: Help Article
                   */}
                  <Route
                    path="/help/article"
                    component={() => {
                      return Article(args);
                    }}
                  />

                  {/**
                   * Route: Password reset
                   */}
                  <Route
                    exact
                    path="/reset"
                    component={() => {
                      return PasswordReset(args);
                    }}
                  />

                  {/**
                   * Default route
                   */}
                  <Route
                    path="*"
                    component={() => {
                      return signedIn ? Summary(args) : Login(args);
                    }}
                  />
                </Switch>
                <Footer {...args} />
              </div>
            </div>
          </div>
        </Router>
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
